import './styles.scss';
import React from 'react';

// import ButtonTheme from '../ButtonTheme';

import jhay from '../../assets/images/jacob.jpg';
const Header = () => {
  function aniosPasados(fecha) {
    const hoy = new Date();
    const anios = new Date(fecha);
    let edad = hoy.getFullYear() - anios.getFullYear();
    const m = hoy.getMonth() - anios.getMonth();

    if (m < 0 || (m === 0 && hoy.getDate() < anios.getDate())) {
      edad--;
    }

    return edad;
  }

  return (
    <div className="header__container">
      {/* <ButtonTheme className="header__button" /> */}
      <div className="header__content size__container">
        <img src={jhay} alt="ing jonathan jacob" className="picture__image" />

        <div className="name">
          <h2>Jonathan Jacob</h2>
          <h1>Ing. de Software</h1>
          <p className="font-sans text-lg max-w-xl leading-5 mt-3">
            Soy un desarrollador fullstack con mas de {aniosPasados('02-16-2018')} años de
            experiencia, orientado a tecnologías web, entusiasta en aprender nuevas
            tecnologías, frameworks, librerías, y con facilidad de adaptación a los cambios.
          </p>
        </div>
      </div>
    </div>
  );
};

export default Header;
